import { Icon } from 'antd';


const GithubLinkIcon = () => {
  return (
    <div className="github-icon">
      <a link="https://github.com/" className="link">
        <Icon type="github" className="github-icon"/>
      </a>
      <style jsx>
        {`
          .github-icon {
            margin-right: 16px;
            line-height: 1;
          }
          .link {
            color: #fff;
            font-size: 32px;
            border: none;
          }
        `}
      </style>
    </div>
  )  
}

export default GithubLinkIcon